
export const listener = () => {
    document.body.addEventListener(('click'), (e) => {
        let target = e.target;
        if (target.classList.contains('tabs__btn')) changeTabPrice(target);
        if (target.classList.contains('information__btn')) changeTabInfo(target);
        if (target.dataset.accordionItem) initAccordion(target);
        if (target.dataset.filter) showOptions(target);
        if (target.classList.contains('filter-title')) closeFilterBlock(target);
        if (target.classList.contains('sub-item')) openMobileMenu(target);
        if (target.classList.contains('modal__close') || target.classList.contains('modal--active')) {
            document.querySelector('.modal--active').classList.remove('modal--active');
        }

        function openMobileMenu(target) {
            const submenu = target.querySelector('.submenu-mob');
            const link = target.querySelector('a');

            link.classList.toggle('_active');
            submenu.classList.toggle('showed');
        }

        function showOptions(target) {
            const parent = target.closest('.filter-options');
            const options = parent.querySelector('.options');

            options.classList.toggle('active');


        }

        function closeFilterBlock(target) {
            const filter = target.closest('.filter');
            const options = filter.querySelectorAll('.filter-options');

            options.forEach(item => {
                item.classList.toggle('hidden');
            })
        }

        function changeTabPrice(target) {
            let currentId = target.dataset.tabBtnPrice;
            let element = document.querySelector(`[data-tab-el-price="${currentId}"]`);
            let activeEl = document.querySelector('.tabs__item.active');

            let activeBtn = document.querySelector('.tabs__btn._is-active');

            if (activeEl) activeEl.classList.remove('active');
            element.classList.add('active');

            if(activeBtn) activeBtn.classList.remove('_is-active');
            target.classList.add('_is-active')

        }

        function changeTabInfo(target) {
            let currentId = target.dataset.tabBtnInfo;
            let element = document.querySelector(`[data-tab-el-info="${currentId}"]`);
            let activeEl = document.querySelector('.information__tab._active');

            let activeBtn = document.querySelector('.information__btn._active');

            if (activeEl) activeEl.classList.remove('_active');
            element.classList.add('_active');

            if(activeBtn) activeBtn.classList.remove('_active');
            target.classList.add('_active')

        }

        function initAccordion(target) {
            const activeEl = document.querySelector('.faq__item--active');

            if (activeEl) activeEl.classList.remove('faq__item--active');

            target.classList.toggle('faq__item--active');
        }
    })
}

listener();