import {HvrSlider} from "./product-slider";

const priceTabsBtns = document.querySelectorAll('.tabs__btn');
const priceTabsItems = document.querySelectorAll('.tabs__item');
const informationBtn = document.querySelectorAll('.information__btn');
const informationTabs = document.querySelectorAll('.information__tab');

function setAttributeTabs(elements1, elements2, slug) {
    elements1.forEach((el, i) => {
        el.setAttribute(`data-tab-btn-${slug}`, `${i + 1}`);
    })

    elements2.forEach((el, i) => {
        el.setAttribute(`data-tab-el-${slug}`, `${i + 1}`);
    })
}

setAttributeTabs(priceTabsBtns, priceTabsItems, 'price');
setAttributeTabs(informationBtn, informationTabs, 'info');

